.map-container{
  width: 100%;
  height: 100vh;
  text-align: center;
  margin-top: $mt-contents;

  .subtitle{
    margin-top: $mt-contents;
    font-size: 20px;
    background-color: $base-bgcolor;
    height: 80px;
    line-height: 80px;
  }

  .map {
    margin-top: $mt-contents;
  }
}

@media screen and (max-width: 800px) {
  .map {
    width: 90%;
    height: 80%;
  }
}
